.app-wrapper {
  margin: 0 auto;
  width: 1200;
  display: grid;

  grid-template-areas:
  "h h"
  "n c";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
   /* grid-gap: 10px; */

}

.app-wrapper-content {
  grid-area: c;
  background-color: rgb(155, 117, 149);
  font-family: 'Anton', sans-serif;
}
